.search-close {
    border: 1px solid var(--black-white);
    border-radius: 61px;
    border-right: none;
    width: 542px;
    padding-left: 2.5%;
  }
  
  .search-open {
    border: 1px solid var(--black-white);
    border-radius: 25px;
    width: 542px;
    padding-left: 2.5%;
  }
  
  .container-searched {
    flex: 1;
  }
  
  .input-search {
    font-size: 18px;
    border: none;
    width: 100%;
    height: 52px;
    background: none;
    color: var(--black-white);
    outline: none;
    padding-right: 17.5%;
    padding-left: 6%;
    transform: translateX(-22px);
    border-radius: 61px;
  }
  
  .first-input {
    padding-right: 3%;
    padding-left: 1%;
    transform: translateX(0px);
    border-bottom: 1px solid var(--grey);
    border-radius: 0;
  }
  
  .btn-search {
    width: 86px;
    padding: 2.5%;
    background: var(--violet);
    border-left: 1px solid var(--black-white);
    border-radius: 0 61px 61px 0;
    /* transform: translateX(0.5px); */
    border-right: 0;
    border-top: 0;
    border-bottom: 0;
    height: 52px;
  }
  
  .btn-open {
    border-radius: 0 23px 0px 0;
  }
  
  .btn-search:hover {
    background: var(--green);
    cursor: pointer;
  }
  
  .icon-search {
    width: 100%;
    height: 100%;
    background: none;
  }
  